<template>
  
    <div class='nav-wrapper'>
     <div class='all-button'  @click='changeBrand("all")'>
     <span :class='{"active-brand":chosedBrand=="all" }'>全部</span></div>
     <div class='bs-wrapper' ref='wrapper'>
       <ul class='brand-list content' v-if='brandList.length' >
          <li class='item-area'>品牌区</li>
          <li v-for='item in brandList' @click='changeBrand(item.brandId)' :class='{"active-brand":chosedBrand==item.brandId }'>{{item.brandName}}</li>
       </ul>
     </div>
      
     <button class='join-btn' >我要入驻</button>
  </div>
 

</template>

<script>
import BScroll from 'better-scroll'


export default {
  data() {
    return {
      chosedBrand:'all'
    }
  },
  computed: {
    // chosedBrand() {
    //   return this.$store.state.chosedBrand
    // },
    brandList() {
      return this.$store.getters.brandList
    }
  },
  methods: {
    changeBrand(brand) {
      var name = this.$route.name,brandId = this.$route.params.brandId

      if(name=='brandDetail'&&brandId==brand){
        return
      }
      if(name=='all'&&brand=='all'){
        return
      }
      // this.$store.commit('changeBrand',brand)
      brand=='all'?this.$router.push({name:'all'}):this.$router.push({name:'brandDetail',params:{brandId:brand}}) 
      this.chosedBrand = brand
      console.log(this.chosedBrand)   
    },
    init_sc() {
      this.$nextTick(()=>{
      this.scroll = new BScroll(this.$refs.wrapper,{click:true,preventDefault:false})
      })
    }
  },
  watch:{
    brandList(newList) {
      if(newList.length) {
        if(this.scroll){
          this.scroll.destroy()
        }
        this.init_sc()
      }
    },
    $route(to) {
      var brand=to.params.brandId
      if(!brand){
        this.chosedBrand = 'all'
      }else{
        this.chosedBrand = brand
      }
    }
  }
}
</script>
<style>
  .nav-wrapper {
    width: 1.7rem;
    height: 7rem;
    position: relative;
   background-color: #F7F7FA;
  }
  .bs-wrapper {
    width: 1.7rem;
    height: 5.7rem;
    overflow: hidden;
  }
  .brand-list {
    width: 1.7rem;
    padding: 0.1rem 0.2rem;
  }
  .brand-list li {
    width: 100%;
    height: 0.64rem;
    margin-bottom:0.23rem;
    text-align: center;
    line-height: 0.64rem;
    font-weight: bold;
  }
  .all-button {
    padding: 0.1rem 0.2rem;
  }
  .all-button>span {
    display: block;
    width: 1.3rem;
    height: 0.64rem;
    text-align: center;
    line-height: 0.64rem;
    font-weight: bold;
  }
  .join-btn {
    height: 0.64rem;
    width: 1.7rem;
    position: absolute;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #FFF;
    border:none;
    border-top:2px solid #2FC27C;
    outline-style: none;
    color:  #2FC27C;
    font-size: 16px;
    
  }
  .active-brand {
    background-color: #2FC27C;
  }
  .item-area {
    color: silver;
  }
</style>


