<template>
  <div class='series-wrapper' ref='wrapper'>
    <ul class=''>
      <li v-for='item in seriesList' class='series-card' @click='changeSeries(item.seriesId)'>
        <img :src='item.avatar' alt="">
        <span class='series-name'>{{item.seriesName}}</span>
      </li>
    </ul>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  props: ['brandId'],
  data() {
    return {

    }
  },
  computed: {
    seriesList() {
      return this.$store.getters.seriesList(this.brandId)
    },
  },
  methods: {
    changeSeries(seriesId) {
     
      this.$router.push({name:'seriesDetail',params:{seriesId,brandId:this.brandId}})
    },
    init_serieScroll() {
      this.$nextTick(() => {
        this.scroll = new BScroll(this.$refs.wrapper, { click: true })
      })
    }
  },
  watch: {
    seriesList(newList) {
      if (newList.length) {
        this.init_serieScroll()
      }
    }
  }
}

</script>
<style>
.series-wrapper {
  width: 4.1rem;
  height: 7rem;
  overflow: hidden;
  padding: 0.2rem 0.2rem 0 0.2rem;
}

.series-card {
  width: 3.75rem;
  height: 2.1rem;
  /*margin:0.2rem 0.2rem 0 0.2rem;*/
  margin-bottom: 0.2rem;
  background-color: #fff;
  position: relative;
}

.series-card img {
  display: block;
  width: 100%;
  height: 100%;
}

.series-name {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 0.5rem;
  color: white;
  background-color: rgba(168, 168, 168, 0.8);
  text-align: center;
  font-weight: bold;
  line-height: 0.5rem;
}

</style>



