<template>
  <div id="app">
    <router-view name='main'></router-view>
    <router-view name='nav'></router-view>
  </div>
</template>
<script>
import brandList from './components/brand-list.vue'
export default {
  components: {
    'brand-list': brandList
  },
  watch: {
    $route(to) {
      console.log('route', to)
    },
  },
  created() {
    var arr = this.$store.state.sellerList
    console.log('获取商家列表 home', arr)
    if (!arr.length) {
      this.$store.dispatch('getSellerList')
    }
  }
}

</script>
<style>
body,
html {
  margin: 0;
  padding: 0;
  width: 5.8rem;
  height: 7rem;
  overflow: hidden;
}

#app {
  width: 5.8rem;
  height: 7rem;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

* {
  box-sizing: border-box;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media screen and (max-width:321px) {
  #app {
    font-size: 14px
  }
}

@media screen and (min-width:321px) and (max-width:400px) {
  #app {
    font-size: 15px
  }
}

@media screen and (min-width:400px) {
  #app {
    font-size: 16px
  }
}

</style>



