import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { getSellerList, getSellerDetial } from '../request.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    sellerData: null,
    seller: 'all',
    sellerList: [],
    sellerDetail: []
  },
  getters: {
    brandList: (state) => {
      var seller = state.seller
      var detail = state.sellerDetail
      if (seller == 'all' && detail && detail.length) {
        console.log('seller为all')
        var brand = []
        detail.forEach(it => {
          brand = brand.concat(it.brands)
        })
        return brand
      } else if (seller !== 'all' && detail && detail.length) {
        console.log('seller 为', seller)
        return detail.filter(it => it.sellerId == seller)[0].brands
      } else {
        return []
      }

    },

    seriesList: (state, getters) => (brandId) => {
      var brands = getters.brandList
      if (brands.length) {
        return brands.filter(it => it.brandId == brandId)[0].seriesList
      } else {
        return []
      }
    },

  },
  mutations: {
    getSellerInfo(state, seller) {
      state.sellerData = seller
      console.log(state.sellerData)
    },

    setSellerList(state, data) {
      state.sellerList = data
    },
    setSellerDetail(state, data) {
      state.sellerDetail = data
    },
    setSeller(state, sellerId) {
      state.seller = sellerId
    }
  },
  actions: {
    getSellerList: ({ commit, state }) => {
      return new Promise((resolve, reject) => {
        getSellerList().then(res => {
          console.log('actions res',res)
          return getSellerDetial(res)
        })
      })
    }
  }
})



