<template>
  <div class='select-wrapper'>
    <div class='select-btn'>
      <div class='space-btn' :class='{"active-select":onSpaceChosing,"condition-chosed":onSpaceChosed}' @click='toggleSpace'>空间
      </div>
      <div class='category-btn' :class='{"active-select":onCategoryChosing,"condition-chosed":onCategoryChosed}' @click='toggleCategory'>品类
      </div>
    </div>
    <div class='select-list-wrapper' ref='select_wrapper'>
      <ul class='select-list'>
        <li v-for='item in products' class='select-item'>
          <img :src='item.avatar' alt="">
          <span>{{item.productName}}</span>
        </li>
      </ul>
    </div>
    <div class='space-wrapper select-conditions' :class='{"active-conditions":onSpaceChosing}'>
      <ul class="condition-list">
        <li class='condition-item' :class='{"chosed-codition-item":chosedSpace==="all"}' @click='spaceChose("all")'>
          <span class='icon'></span>
          <span class='condition-name'>全部</span>
        </li>
        <li v-for='item in spaces' class='condition-item' :class='{"chosed-codition-item":chosedSpace===item.spaceId}' @click='spaceChose(item.spaceId)'>
          <span class='icon'></span>
          <span class='condition-name'>{{item.spaceName}}</span>
        </li>
        <li class='condition-item' :class='{"chosed-codition-item":chosedSpace===""}' @click='spaceChose("")'>
          <span class='icon'></span>
          <span class='condition-name'>其它</span>
        </li>
      </ul>
    </div>
    <div class='category-wrapper select-conditions' :class='{"active-conditions":onCategoryChosing}'>
      <ul class="condition-list">
        <li class='condition-item' :class='{"chosed-codition-item":chosedCategory==="all"}' @click='categoryChose("all")'>
          <span class='icon'></span>
          <span class='condition-name'>全部</span>
        </li>
        <li v-for='item in categorys' class='condition-item' :class='{"chosed-codition-item":chosedCategory===item.categoryId}' @click='categoryChose(item.categoryId)'>
          <span class='icon'></span>
          <span class='condition-name'>{{item.categoryName}}</span>
        </li>
        <li class='condition-item' :class='{"chosed-codition-item":chosedCategory===""}' @click='categoryChose("")'>
          <span class='icon'></span>
          <span class='condition-name'>其它</span>
        </li>
      </ul>
    </div>
    <div id="more" @click='loadNewPage'>{{moreData?'加载更多':'无更多数据'}},{{onloadingPage?'正在加载':'没有加载'}}</div>
  </div>
</template>
<script>
import BSscroll from 'better-scroll'
import { getProducts } from '../request.js'
export default {
  props: ['seriesId', 'brandId'],
  data() {
    return {
      onSpaceChosing: false,
      onCategoryChosing: false,
      onSpaceChosed: false,
      onCategoryChosed: false,
      gettingPages: false,
      total: 0,
      curPage: 0,
      pageY: 0,
      onloadingPage: false,
      categorys: [],
      spaces: [],
      products: [],
      perPage: 30,
      chosedSpace: 'all',
      chosedCategory: 'all',
      _scroll:''
    }
  },
  created() {
    console.log('selectlist created', this.conditions)
    this.getSelectList(this.conditions)
  },
  methods: {
    toggleSpace() {
      this.onCategoryChosing = false
      if (this.onSpaceChosing) {
        this.onSpaceChosing = false
      } else {
        if (this.onSpaceChosed) {
          this.onSpaceChosed = false
          this.changeSpace('all')
        } else {
          this.onSpaceChosing = true
        }
      }
    },
    toggleCategory() {
      this.onSpaceChosing = false
      if (this.onCategoryChosing) {
        this.onCategoryChosing = false
      } else {
        if (this.onCategoryChosed) {
          this.onCategoryChosed = false
          this.changeCategory('all')
        } else {
          this.onCategoryChosing = true
        }
      }
    },
    getSelectList(params) {
      var self = this
      getProducts(params).then(res => {

        self.total = +res.data.total
        self.curPage = +res.data.page
        var spaces = res.data.spaces,
          categorys = res.data.categorys
        // self.$store.commit('getSelectList', res.data.products)
        self.categorys = categorys
        self.spaces = spaces
        self.products = res.data.products
        console.log('select products', this.products)
        this.init_select_scroll(false)
      }).catch(erro => {
        console.log('获取数据失败', erro)
      })
    },
    getNextPage(params) {
      var self = this
      getProducts(params).then(res => {
        console.log(res.data)
        self.total = +res.data.total
        self.curPage = +res.data.page
        self.onloadingPage = false
        var products = res.data.products
        self.products = self.products.concat(products)
        this.init_select_scroll(true)
      }).catch(erro => {
        console.log('获取数据失败', erro)
      })
    },
    loadNewPage() {

      if (!this.moreData) {
        return
      }
      var temp = this.conditions
      var params = {}
      Object.keys(temp).forEach(it => {
        if (it == 'page') {
          params[it] = temp[it] + 1
        } else {
          params[it] = temp[it]
        }
      })

      this.getNextPage(params)
    },
    changeSpace(space) {
      this.chosedSpace = space
      this.curPage = 0
      this.getSelectList(this.conditions)
    },
    changeCategory(category) {
      this.chosedCategory = category
      this.curPage = 0
      this.getSelectList(this.conditions)
    },
    spaceChose(space) {
      this.onSpaceChosing = false
      if (space !== 'all') {
        this.onSpaceChosed = true
      }
      this.changeSpace(space)
    },
    categoryChose(category) {
      this.onCategoryChosing = false
      if (category !== 'all') {
        this.onCategoryChosed = true
      }
      this.changeCategory(category)
    },
    init_select_scroll(flag) {
      var self = this
      this.$nextTick(() => {
        if (!this._scroll) {
          this._scroll = new BSscroll(this.$refs.select_wrapper, { click: true, bounce: false, momentum: false, probeType: 2 })
          this._scroll.on('scroll', e => {
            if (self.moreData && !self.onloadingPage && this._scroll.y - 100 < self._scroll.maxScrollY) {
              self.onloadingPage = true
              self.loadNewPage()
            }
          })
        } else if (flag) {
          this._scroll.refresh()
        } else {
          this._scroll.destroy()
          this._scroll = new BSscroll(this.$refs.select_wrapper, { click: true, bounce: false, momentum: false, probeType: 2 })
          this._scroll.on('scroll', e => {
            if (self.moreData && !self.onloadingPage && this._scroll.y - 100 < self._scroll.maxScrollY) {
              self.onloadingPage = true
              self.loadNewPage()
            }
          })
        }

      })
    }
  },
  computed: {
    moreData() {
      return this.products.length < this.total
    },
    selectList() {
      return this.$store.state.selectList
    },
    spaceList() {
      return this.$store.state.spaceList
    },
    categoryList() {
      return this.$store.state.categoryList
    },
    seller() {
      return this.$store.state.seller
    },
    conditions() {
      var perPage = this.perPage,
        page = this.curPage,
        seriesId = this.seriesId,
        brandId = this.brandId,
        seller = this.$store.state.seller,
        categoryId = this.chosedCategory,
        spaceId = this.chosedSpace
      var res = {}
      res.perPage = perPage
      res.page = page
      if (seriesId) {
        res.seriesId = seriesId
      }
      if (brandId) {
        res.brandId = brandId
      }
      if (seller !== 'all') {
        res.sellerId = seller
      } else {}
      if (categoryId !== 'all') {
        res.categoryId = categoryId
      }
      if (spaceId !== 'all') {
        res.spaceId = spaceId
      }
      return res
    }

  },
  watch: {
    seller (newseller) {
      this.onSpaceChosing= false
      this.onCategoryChosing= false
      this.onSpaceChosed= false
      this.onCategoryChosed= false
      this.gettingPages= false
      this.total= 0
      this.curPage= 1
      this.onloadingPage= false
      this.categorys= []
      this.spaces= []
      this.products= []
      this.perPage= 30
      this.chosedSpace= 'all'
      this.chosedCategory= 'all'
      this.getSelectList(this.conditions)
    }
  },

}

</script>
<style>
.select-wrapper {
  width: 4.1rem;
  height: 100%;
  position: relative;
}

.select-btn {
  width: 100%;
  height: 0.85rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
  background-color: #fff;
  border-bottom: 1px solid silver;
}

.space-btn,
.category-btn {
  width: 1.7rem;
  height: 0.55rem;
  padding-left: 0.2rem;
  background-color: #F1F4F8;
  line-height: 0.55rem;
  position: relative;
}

.space-btn::after,
.category-btn::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  right: 0.15rem;
  top: 0.2rem;
  border: 0.08rem solid transparent;
  border-left: 0.1rem solid black;
}

.active-select::after {
  top: 0.25rem;
  right: 0.2rem;
  transform: rotate(90deg);
}

.select-wrapper>.active-conditions {
  display: block;
}

.select-conditions {
  width: 4.1rem;
  height: 6.16rem;
  overflow: scroll;
  position: absolute;
  left: 0;
  top: 0.85rem;
  background-color: #fff;
  display: none;
}

.select-list-wrapper {
  width: 100%;
  height: 6.3rem;
  overflow: hidden;
}

.select-list,
.condition-list {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  padding-bottom: 0.2rem;
}

.select-item {
  width: 50%;
  height: 2.3rem;
  display: flex;
  border: 1px solid silver;
  flex-direction: column;
  align-items: center;
  margin-top: -1px;
  justify-content: space-around;
}

.select-list li:nth-child(2n) {
  margin-left: -1px;
}

.select-item>img {
  width: 1.35rem;
  height: 1.35rem;
}

.select-item>span {
  width: 80%;
  height: 0.6rem;
  text-align: center;
  font-weight: normal;
  font-size: 12px
}

.condition-list {
  padding-top: 0.5rem;
}

.condition-item {
  width: 25%;
  height: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.condition-item .icon {
  width: 70%;
  height: 40%;
  border-radius: 50%;
  background-color: gray;
}

.condition-item .condition-name {
  width: 70%;
  height: 30%;
  text-align: center;
  font-size: 12px;
}

.chosed-codition-item {
  color: red;
}

.condition-chosed::after {
  content: "x";
  position: absolute;
  width: 0.2rem;
  height: 0.2rem;
  text-align: center;
  right: 10%;
  top: 30%;
  font-size: 0.2rem;

  line-height: 0.16rem;
  border: 1px solid red;
  border-radius: 50%;
  color: red;
  font-weight: normal;
}

#more {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 0.5rem;
  background-color: silver;
  opacity: 0.5;
}

</style>



