import Vue from 'vue'
import Router from 'vue-router'
import brandList from '@/components/brand-list.vue'
import selectList from '@/components/select-list.vue'
import seriesList from '@/components/series-list.vue'
import seriesView from '@/components/series-view.vue'
import homeMain from '@/components/home-main.vue'
Vue.use(Router)


var newroute = {
  base: '/modules/main',

  routes: [{
      path: '/',
      redirect: '/home'

    },
    {
      path: '/home',
      components: {
        main: homeMain,
        nav: brandList
      },
      children: [{
          path: '',
          name: 'all',
          component: selectList
        },
        {
          path: ':brandId',
          component: seriesView,
          props: true,
          children: [{
              path: '',
              name: 'brandDetail',
              component: seriesList,
              props: true
            },
            {
              path: ':seriesId',
              name: 'seriesDetail',
              component: selectList,
              props: true
            }
          ]
        },
      ]


    },
    // {
    //   path:'/seller',
    //   components:{
    //     main:
    //   },
    // }
  ]
}
var route = {

  routes: [{
      path: '/home',
      name: 'home',
      component: selectList
    },
    {
      path: '/:brandId',
      component: seriesView,
      props: true,
      children: [{
          path: '',
          name: 'brandDetail',
          component: seriesList,
          props: true
        },
        {
          path: ':seriesId',
          name: 'seriesDetail',
          component: selectList,
          props: true
        }
      ]
    },
  ]
}

export default new Router(newroute)



