import axios from 'axios'
import store from './store'

const service = axios.create({
    baseURL: 'http://api.lm2ar.com/api',
    timeout: 15000 // 请求超时时间
})


service.interceptors.response.use(
    response => {
        /**
         * code为非20000是抛错 可结合自己业务进行修改
         */
        const res = response.data
        if (res.code > 300) {
            return Promise.reject(res.msg)
        } else {
            return response.data
        }
    },
    error => {
        console.log('err' + error) // for debug
        return Promise.reject(error)
    }
)
export function getSellerInfo(sellerId) {
	return service({
		url:`/seller/${sellerId}`,
	}).then(res=>{
		store.commit('getSellerInfo',res.data)
	}).catch(error=>{
		console.log('获取sellerData失败',error)
	})
}

export function getSellerList() {
	return service({
		url:'/sellerList'
	}).then(res=>{
		console.log('获取商家列表',res)
		store.commit('setSellerList',res.data.sellerList)
		return res.data.sellerList
	}).catch(erro=>{
		console.log('获取商家列表失败！',erro)
	})
}
export function getSellerDetial(ids) {
	console.log('detail 开始')
	return axios.all(all(ids)).then(axios.spread((...arr)=>{
		var res = arr.map(it=>it.data)
		store.commit('setSellerDetail',res)
		console.log('sellerDetail',store.state.sellerDetail)
	})).catch(error=>{
		console.log('all 错误',error)
	})
}

function all(ids) {
	return ids.map(it=>{
		return service({
				url:`/seller/${it}`
			})
	})
}

export function getProducts(params) {
	console.log('params',params)
	return service({
		url:'/products',
		params
	})
}


